import { BackProvider } from './../../providers/back/back';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { AngularFirestore } from 'angularfire2/firestore';
import { Collections, User } from '../../app/app.models';
import { DataProvider } from '../../providers/data/data';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-checklist',
  templateUrl: 'checklist.html',
})
export class ChecklistPage {

  public checklist: any[]
  public uid: string = localStorage.getItem('uid')

  public completed: string[];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public toast: ToastController,
    public back: BackProvider,
    public load: LoadingController,
    private afs: AngularFirestore,
    private data: DataProvider,
    private auth: AuthProvider,
  ) {
  }

  ionViewDidLoad() {

    const l = this.load.create({content: 'Cargando checklist...'})
    l.present()

    this.afs.collection(Collections.TODO)
      .valueChanges()
      .subscribe(data => {
        this.checklist = data
        l.dismiss()
      })

    this.auth.user$.subscribe(user => {
      if (user && user.completedTasks && !this.completed) this.completed = user.completedTasks;
    })

    /* this.back.getAllTodos().subscribe(data => {
      this.checklist = data.map(t => t.users ? t : ({users: {}, ...t}))
      l.dismiss()
    }, err => {
      this.toast.create({message: 'No se pudo cargar el checklist...', duration: 2000}).present()
      l.dismiss()
      this.navCtrl.setRoot('HomePage')
    }) */

  }

  async toggleCompleted(id: string) {

    console.log(id)

    const userKey = `${Collections.USER}/${this.auth.user.uid}`
    const _user = await this.data.getDocAlt<User>(Collections.USER, this.auth.user.uid);
    const completedTasks: string[] = _user.completedTasks || []

    if (completedTasks.indexOf(id) < 0) {

      completedTasks.push(id)
      await this.afs.doc(userKey).update({ completedTasks })
      /* this.checkChanged.next({ id, added: true }); */

    } else {
      if (completedTasks.length > 0) completedTasks.splice(completedTasks.indexOf(id), 1)
      await this.afs.doc(userKey).update({ completedTasks })
      /* this.checkChanged.next({ id, added: false }); */
    }

    // this.stats.modifyCustomCounter(`event-${id}`, this.event.title, 1)

  }

}



